@import "../../style/variables";

.#{$prefix}menu {
  display: flex;
  flex-wrap: nowrap;
  box-sizing: border-box;
  list-style: none;
  padding: 0;
  margin: 0;
  white-space: nowrap;
  border: 0;

  &_horizontal {
    border-bottom: 1px solid #f0f0f0;

    .#{$prefix}menu-item {
      padding: 15px;
      margin: 0 5px;
      border-bottom: 2px solid transparent;

      &_active {
        color: $primary-color;
        border-bottom-color: $primary-color;
      }
    }

    .#{$prefix}menu-submenu {
      &__menu {
        position: absolute;
        background: #fff;
        z-index: 100;
        top: calc(100% + 2px);
        left: 0;
      }
    }
  }

  &_vertical {
    flex-direction: column;
    border-left: 1px solid #f0f0f0;

    .#{$prefix}menu-item {
      padding: 10px 15px;
      margin: 5px 0;
      border-left: 2px solid transparent;

      &_active {
        color: $primary-color;
        border-left-color: $primary-color;
      }
    }
  }
}

.#{$prefix}menu-item {
  border-bottom: 2px solid transparent;
  position: relative;
  top: 1px;
  white-space: nowrap;
  cursor: pointer;

  &_disabled {

  }
}

.#{$prefix}menu-submenu {
  position: relative;

  &__menu {
    list-style: none;
    padding-left: 0;
    white-space: nowrap;
  }
}
